<template>
  <div class="main-panel">
    <div class="row" style="margin-top:75px;margin-left:30px">
      <div class="col-md-2"></div>
      <div class="col-md-10">
        <div class="card">
          <div class="card-header">
            <h5 class="title">Kategori Ekle</h5>
          </div>
          <div class="card-body">
            <form>
              <div class="row">
                <div class="col-md-5 pr-md-1">
                  <div class="form-group">
                    <label>Şirket </label>
                    <input
                      type="text"
                      class="form-control"
                      disabled=""
                      placeholder="Zeki Usta"
                    />
                  </div>
                </div>
                <div class="col-md-3 px-md-1">
                  <div class="form-group">
                    <label>Kategori Adı</label>
                    <input
                      type="text"
                      class="form-control"
                      placeholder="Kategori Adı"
                      v-model="category.name"
                    />
                  </div>
                </div>
                <div class="col-md-4 pl-md-1">
                  <div class="col-md-12 pl-md-5  ">
                    <div class="form-group">
                      <img
                        height="128"
                        class="img-responsive text-center mb-3"
                        :src="
                          fileUrl == null
                            ? './admin/assets/default.png'
                            : fileUrl
                        "
                      />
                      <input
                        class="form-control"
                        ref="file"
                        type="file"
                        style="display:none"
                        @change="onChange($event)"
                      />
                      <button
                        class="btn btn-outline-secondary"
                        type="button"
                        @click="$refs.file.click()"
                      >
                        Resim Seç
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
          <div class="card-footer text">
            <button
              @click="addProduct"
              type="submit"
              class="btn btn-fill btn-primary"
            >
              Kaydet
            </button>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-2"></div>
      <div class="col-md-9" style="margin-left:20px">
        <div class="card card-user">
          <div class="card-body">
            <div class="table-responsive" style="overflow:auto">
              <table class="table tablesorter " id="">
                <thead class=" text-primary">
                  <tr>
                    <th>
                      Kategori Adı
                    </th>
                    <th>
                      Dosya Yolu
                    </th>
                    <th>
                      Dosya Resmi
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="category in categoryList" :key="category.id">
                    <td>
                      {{ category.name }}
                    </td>
                    <td>
                      <a :href="category.img" target="_blank">{{
                        category.img
                      }}</a>
                    </td>
                    <td>
                      <img
                        height="80"
                        class="img-responsive text-center mb-3"
                        :src="category.img"
                        alt=""
                      />
                    </td>

                    <td class="text-center">
                      <button
                        @click="deleteProduct(category.id)"
                        class="btn btn-fill btn-info"
                      >
                        Sil
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-2"></div>
      <div class="col-md-9 ml-4">
        {{ value }}
        <el-transfer
          v-model="value"
          :data="this.categoryList"
          :props="{ key: 'id', label: 'name' }"
        >
        </el-transfer>
      </div>
    </div>
    <div class="row">
      <div class="col-md-2"></div>
      <div class="col-md-9 ml-4">
        <h2>Test Button</h2>
        <button @click="sortCategory" class="btn btn-fill btn-info">
          Sırala
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import swal from "sweetalert";
import axios from "axios";
export default {
  data() {
    return {
      value: [],
      categoryList: [],
      file: null,
      fileUrl: null,
      category: {
        name: ""
      }
    };
  },
  methods: {
    sortCategory() {
      if (this.value != "") {
        const url = "category/update";
        axios.post(url, { data: this.value }).then(res => {
          console.log(res);
        });
      }
    },
    onChange(e) {
      this.file = e.target.files[0];
      this.fileUrl = URL.createObjectURL(this.file);
    },
    deleteProduct(id) {
      const url = "admin/api/product/del/" + id;

      axios.get(url);
    },

    addProduct() {
      const fd = new FormData();
      fd.append("image", this.file);
      fd.append("category", JSON.stringify(this.category));
      const url = "admin/api/newCategory";
      if (this.category.name != "") {
        axios.post(url, fd).then(res => {
          console.log(res);

          if (res.data.status == "ok") {
            swal("Kategori Başarıyla Eklendi!", "", "success", {
              button: "Devam Et!",
              timer: 1500
            }).then(() => {
              location.reload();
            });
          }
        });
      } else {
        swal("Ürün Eklenemedi!", "", "warning", {
          button: "Devam Et!",
          timer: 1500
        });
      }
    },
    deleteProduct(id) {
      const url = "admin/api/delCategory/" + id;

      axios.get(url).then(res => {
        console.log(res);

        if (res.data.status == "ok") {
          swal("Kategori Başarıyla Silindi!", "", "success", {
            button: "Devam Et!",
            timer: 1500
          }).then(() => {
            location.reload();
          });
        } else {
          swal("Kategori Silinemedi!", "", "warning", {
            button: "Devam Et!",
            timer: 1500
          });
        }
      });
    }
  },
  created() {
    const url = "admin/api/allCateogry";
    const url2 =
      "http://www.json-generator.com/api/json/get/canhbeOESW?indent=2";
    axios.get(url).then(response => {
      this.categoryList = response.data;
      console.log(this.categoryList);
    });
  }
};
</script>

<style></style>
